<template>
  <label for="">
    <label for="" v-for="(item, index) in navigatorInfo" :key="index">
      <label for="" v-if="item.path || !item.children">
        <v-list-item
          :input-value="tag == item.path"
          color="red"
          @click="navGopage(item.path)"
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.name }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </label>
      <v-list-group v-else :sub-group="!item.children">
        <template v-slot:activator>
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-title>{{ item.name }}</v-list-item-title>
        </template>
        <label for="" v-for="(k, m) in item.children" :key="m">
          <label for="" v-if="!k.children">
            <v-list-item
              :input-value="tag == k.path"
              color="red"
              @click="navGopage(k.path)"
              style="padding-left: 2rem"
            >
              <v-list-item-icon>
                <v-icon></v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>{{ k.name }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </label>
          <label for="" v-else>
            <v-list-group sub-group>
              <template v-slot:activator>
                <v-list-item-title style="padding-left: 1.5rem">{{
                  k.name
                }}</v-list-item-title>
              </template>
              <label for="" v-for="(a, x) in k.children" :key="x">
                <v-list-item
                  :input-value="tag == a.path"
                  color="red"
                  @click="navGopage(a.path)"
                >
                  <v-list-item-icon>
                    <v-icon></v-icon>
                  </v-list-item-icon>
                  <v-list-item-content style="padding-left: 1.5rem">
                    <v-list-item-title>{{ a.name }}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </label>
            </v-list-group>
          </label>
        </label>
      </v-list-group>
    </label>
  </label>
</template>

<script>
export default {
  name: "my-nav-list",
  props: {
    navigatorInfo: Array,
    tag: String,
  },
  data() {
    return {};
  },
  methods: {
    navGopage(item) {
      //路由路径参数

      this.$emit("changeTag", item);
      this.$router.push({ path: "/Home/" + item });
    },
    test() {
      console.log(123456);
    },
  },
};
</script>
<style  scoped>
.navBox::-webkit-scrollbar {
  display: none;
}
</style>